<template><section><html><head></head><body><h1>VcScanRadar</h1>
<p>The <code v-pre="">vc-scan-radar</code> component is used to the radar scanning special effects. Refer to <a href="https://www.wellyyss.cn/ysCesium/main/app.html">ysCesium|跃焱邵隼</a>.</p>
<h2>Example</h2>
<h3>Load a VcScanRadar</h3>
<h4>Preview</h4>
<doc-preview>
  <template>
    <div class="viewer">
      <vc-viewer @ready="ready">
        <vc-scan-radar @ready="subReady" :radius="1500" :interval="3000" :color="[0,1.0,0,1]" :position="position"></vc-scan-radar>
        <vc-layer-imagery>
          <vc-provider-imagery-tianditu map-style="img_c" token="436ce7e50d27eede2f2929307e6b33c0"></vc-provider-imagery-tianditu>
        </vc-layer-imagery>
      </vc-viewer>
    </div>
  </template>
  
</doc-preview>
